import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {AppComponent} from './app.component';
import {HomeComponent} from './components/home/home.component';
import {SharedModule} from './shared.module';
import {MANAGE_USERS_ROUTES, ManageUsersModule} from './components/manage-users/manage-users.module';
import {STUDIO_ROUTES, StudioModule} from './components/studio/studio.module';
import {ANALYTICS_ROUTES, AnalyticsModule} from './components/analytics/analytics.module';
import {HotkeyModule} from 'angular2-hotkeys';
import {DEPLOY_ROUTES, DeployModule} from './components/deploy/deploy.module';

const APP_ROUTES: Routes = [
    {path: '', redirectTo: 'home', pathMatch: 'full'},
    {
        path: 'manage-users',
        children: MANAGE_USERS_ROUTES
    },
    {
        path: 'studio',
        children: STUDIO_ROUTES
    },
    {
        path: 'deploy',
        children: DEPLOY_ROUTES
    },
    {
        path: 'analytics',
        children: ANALYTICS_ROUTES
    },
    {path: 'home', component: HomeComponent},
    {path: '**', redirectTo: 'home'}
];

@NgModule({
    declarations: [
        AppComponent,
        HomeComponent
    ],
    imports: [
        SharedModule,
        ManageUsersModule,
        StudioModule,
        AnalyticsModule,
        DeployModule,
        RouterModule.forRoot(APP_ROUTES, {
            useHash: true
        }),
        HotkeyModule.forRoot({
            cheatSheetCloseEsc: true,
        })
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}



